<template>
  <section style="margin-left:15px;margin-right:15px;margin-top: 15px;">
    <el-divider></el-divider>
    <el-descriptions class="margin-top" title="" :column="3" :size="size">
      <template slot="title">
      <span>{{ campaignMeta.campaign.name }}</span>
      </template>
    <template slot="extra">
      <router-link v-if="firstID==='all'" :to="{ name: '营销集市', params: {campaign_id: this.$route.params.campaign_id}}" style='text-decoration: none;color: aliceblue;'>
          <el-button class="return-list"><i class="el-icon-arrow-left" style="margin-right: 5px"></i>返回营销集市</el-button>
      </router-link>
    </template>
    <el-descriptions-item label="创建人">{{campaignMeta.campaign.author}}</el-descriptions-item>
    <el-descriptions-item label="创建时间"><timeago :datetime="campaignMeta.campaign.creation_date" locale="zh-CN"></timeago></el-descriptions-item>
    <el-descriptions-item label="已创建的实验数">{{ campaignMeta.strategy_cnt }}</el-descriptions-item>

    <el-descriptions-item v-if="campaignMeta.campaign.data_campaign_type==='recs'" label="活动类型"><el-tag size="small">策略推荐</el-tag></el-descriptions-item>
    <el-descriptions-item v-else-if="campaignMeta.campaign.data_campaign_type==='promotions'" label="活动类型"><el-tag size="small">个性化促销</el-tag></el-descriptions-item>
    <el-descriptions-item v-else label="活动类型"><el-tag size="small">通用</el-tag></el-descriptions-item>
  </el-descriptions>

  <el-collapse v-model="activeNames" @change="handleChange">
  <el-collapse-item title="活动描述" name="1">
    <span v-show="descvisalble">{{ campaignMeta.campaign.description }}&nbsp;&nbsp;</span>
    <el-button v-show="descvisalble" type="text" @click="handleEdit"><i class="el-icon-edit"></i></el-button>

    <el-input v-show="descEditable" type="textarea" style="width:60%" :rows="3" v-model="editCampaignMeta.description"></el-input>
    <div>
    <el-button type="text"  v-show="descEditable" @click="handelCancel" style="margin-right: 0px;" icon="el-icon-arrow-left">取消</el-button>
    <el-button style="align:right" v-show="descEditable" type="text" icon="el-icon-check" @click="submitSave" circle >保存</el-button>
  </div>
  </el-collapse-item>
  <el-collapse-item title="实验列表" name="2">
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
          <el-form :inline="true" :model="filters" @submit.native.prevent>
              <el-form-item>
                  <el-input v-model="filters.name" placeholder="名称" @keyup.enter.native="getAbExpList"></el-input>
              </el-form-item>
              <el-form-item>
                  <el-button type="primary" @click="getAbExpList">查询</el-button>
              </el-form-item>
              <el-form-item>
                  <el-button type="primary" @click="handleAdd" icon="el-icon-plus">数据科学实验</el-button>
              </el-form-item>
          </el-form>
      </el-col>
    <el-table :data="AbExpList" highlight-current-row
                v-loading="listLoading"
                @expand-change="expandExpClick"
                style="width: 100%;">
          <el-table-column type="expand">
              <template slot-scope="props">
                  <small><b>实验描述:</b>
                      {{props.row.description}}
                  </small>
                  <br>
                  <el-table :data="altListData" highlight-current-row style="width: 100%;">
                      <el-table-column prop="alt_name" label="实验组名称" min-width="30%">
                      </el-table-column>
                      <el-table-column prop="exposure_ratio" label="曝光率(%)" min-width="30%">
                      </el-table-column>
                       <el-table-column prop="model_name" label="使用模型" min-width="40%">
                      </el-table-column>
                  </el-table>
              </template>
          </el-table-column>

          <el-table-column type="index" width="55">
          </el-table-column>
          <el-table-column prop="name" label="实验名称" min-width="15%" sortable show-overflow-tooltip>
              <template slot-scope="scope">
                  <el-icon name="name"></el-icon>
                  <router-link :to="{ name: '实验详情', params: {ab_id: scope.row.id,campaign_id: scope.row.data_campaign_id}}" style="cursor:pointer;color: #0000FF;text-decoration:underline">{{ scope.row.name }}
                  </router-link>
              </template>
          </el-table-column>
          <el-table-column prop="id" label="实验编号" min-width="10%">
          </el-table-column>
          <el-table-column prop="altlist" label="实验类型" min-width="15%" sortable show-overflow-tooltip>
              <template slot-scope="scope">
                <el-tag :type="scope.row.app_type==='基于基于数据策略' ? 'primary' : 'success'">{{scope.row.app_type}}&nbsp;</el-tag>
                  <!--<el-tag :type="scope.row.app_type==='mlExp' ? 'primary' : 'success'">{{scope.row.app_type==='mlExp'?'普通':'模型'}}&nbsp;</el-tag>-->
              </template>
          </el-table-column>
          <el-table-column prop="result" label="创建时间" min-width="19%" sortable show-overflow-tooltip>
              <template slot-scope="scope">
                  <span style="font-size: 16px;"><timeago :datetime="scope.row.creation_date" locale="zh-CN"></timeago></span>
              </template>
          </el-table-column>

          <el-table-column prop="description" label="实验描述" min-width="21%" sortable>
              <template slot-scope="scope">
                  <span style="font-size: 16px;">{{scope.row.description}}</span>
              </template>
          </el-table-column>

          <el-table-column label="操作" min-width="20%" align='right'>
              <template slot-scope="scope">
                  <el-button v-if="scope.row.abalt_cnt<2" type="primary" style="text-align:right" size="small" @click="handelAddExpGroup(scope.$index, scope.row)" icon="el-icon-plus">实验组</el-button>
                  <el-button v-else type="info" style="text-align:right" size="small" @click="handelAddExpGroup(scope.$index, scope.row)" icon="el-icon-plus">实验组</el-button>
                  <el-button type="danger" style="text-align:right" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
              </template>
          </el-table-column>
      </el-table>
      <el-col :span="24" class="toolbar">
          <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :page-count="total" style="float:right;">
          </el-pagination>
      </el-col>
  </el-collapse-item>
</el-collapse>
<!--新增界面-->
<el-dialog title="新增数据实验" :visible.sync="addFormVisible" :close-on-click-modal="false" style="width: 75%; left: 12.5%">
          <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
              <el-form-item label="实验名称" prop="name">
                  <el-input v-model.trim="addForm.name" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label="描述" prop='description'>
                  <el-input type="textarea" :rows="3" v-model="addForm.description"></el-input>
              </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
              <el-button @click.native="addFormVisible = false">取消</el-button>
              <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
          </div>
      </el-dialog>

  </section>
</template>

<script>
// {{moment(scope.row.updateTime).format("YYYY-MM-DD HH:mm:ss")}}
import { test, addAbExp, EditCampaignDesc } from '@/api/api'
import $ from 'jquery'
import moment from 'moment'
export default {
  data () {
    return {
      descvisalble: true,
      descEditable: false,
      campaignMeta: {},
      editCampaignMeta: {
        description: ''
      },
      addFormVisible: false, // 新增界面是否显示
      addLoading: false,
      addFormRules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' },
          { min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur' }
        ],
        description: [
          { required: false, message: '请输入版本号', trigger: 'blur' },
          { max: 1024, message: '不能超过1024个字符', trigger: 'blur' }
        ]
      },
      // 新增界面数据
      addForm: {
        name: '',
        description: ''
      },
      activeNames: ['1', '2'],
      firstID: 'all',
      altListData: [],
      moment: moment,
      project: '',
      case: '',
      AbExpList: [],
      listLoading: false,
      filters: {
        name: ''
      },
      total: 0,
      page: 1,
      sels: [], // 列表选中列
      activeIndex: ''
    }
  },
  methods: {
    handelAddExpGroup (index, row) {
      if (row.abalt_cnt > 0) {
        this.$message({
          message: '该实验已经创建，请创建新实验',
          type: 'warning'
        })
      } else {
        let self = this
        self.$router.push({ name: '创建实验服务',
          params: { campaign_id: self.$route.params.campaign_id, ab_id: row.id } })
      }
    },
    // 新增
    submitSave: function () {
      let self = this
      // NProgress.start();
      let params = JSON.stringify({
        campaign_id: self.$route.params.campaign_id,
        description: self.editCampaignMeta.description,
        user_name: JSON.parse(sessionStorage.getItem('name'))
      })
      console.log(params, 'params')
      let header = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      EditCampaignDesc(header, params).then(_data => {
        let { msg, code } = _data
        self.addLoading = false
        if (code === '999999') {
          self.$message({
            message: '修改成功',
            center: true,
            type: 'success'
          })
          this.descEditable = false
          this.descvisalble = true
          this.editCampaignMeta.description = ''
          self.getCampaignMata()
          self.getAbExpList()
        } else if (code === '999997') {
          self.$message.error({
            message: msg,
            center: true
          })
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
          this.editCampaignMeta.description = ''
          this.descEditable = false
          this.descvisalble = true
          self.getCampaignMata()
          self.getAbExpList()
        }
      })
    },
    handelCancel: function () {
      this.descEditable = false
      this.editCampaignMeta.description = ''
      this.descvisalble = true
    },
    handleEdit: function () {
      this.descEditable = true
      this.editCampaignMeta.description = this.campaignMeta.campaign.description
      this.descvisalble = false
    },
    // 获取活动的元数据
    getCampaignMata () {
      let self = this
      let param = { campaign_id: self.$route.params.campaign_id }
      $.ajax({
        type: 'get',
        url: test + '/api/campaigns/get_campaign_metadata',
        async: true,
        data: param,
        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          if (data.code === '999999') {
            self.campaignMeta = data.data.data
            console.log(self.campaignMeta, 'self.users')
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },
    // 显示新增界面
    handleAdd: function () {
      this.addFormVisible = true
    },
    // 新增
    addSubmit: function () {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          let self = this
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            self.addLoading = true
            // NProgress.start();
            let params = JSON.stringify({
              name: self.addForm.name,
              campaign_id: self.$route.params.campaign_id,
              description: self.addForm.description,
              user_name: JSON.parse(sessionStorage.getItem('name'))
            })
            let header = {
              'Content-Type': 'application/json',
              Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
            }
            addAbExp(header, params).then(_data => {
              let { msg, code } = _data
              self.addLoading = false
              if (code === '999999') {
                self.$message({
                  message: '添加成功',
                  center: true,
                  type: 'success'
                })
                self.$refs['addForm'].resetFields()
                self.addFormVisible = false
                self.getAbExpList()
              } else if (code === '999997') {
                self.$message.error({
                  message: msg,
                  center: true
                })
              } else {
                self.$message.error({
                  message: msg,
                  center: true
                })
                self.$refs['addForm'].resetFields()
                self.addFormVisible = false
                self.getAbExpList()
              }
            })
          })
        }
      })
    },
    handleChange (val) {
      console.log(val)
    },
    LookExpDetails (index, row) {
      let self = this
      self.$router.push({ name: '查看实验详情',
        params: { api_id: row.id } })
    },
    expandExpClick (val) {
      this.getExpAltList(val.id)
    // console.log("expandExpClick",this.altListData,val.id)
    },
    getAbExpList () {
      this.listLoading = true
      let self = this
      console.log(self.page, this.$route.params.case_id, 'datadatadata')
      $.ajax({
        type: 'get',
        url: test + '/api/campaigns/abexps',
        async: true,
        data: { campaign_id: this.$route.params.campaign_id,
          page: self.page,
          name: self.filters.name,
          user_name: JSON.parse(sessionStorage.getItem('name'))
        },
        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          self.listLoading = false
          if (data.code === '999999') {
            self.AbExpList = []
            self.total = data.data.total
            data.data.data.forEach((item) => {
              item.result = false
              self.AbExpList.push(item)
            })
            console.log(self.AbExpList, 'self.AbExpList')
          // self.ApiList = data.data.data
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },
    handleDel (index, row) {
      this.$confirm('确认删除该实验吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        // NProgress.start();
        let self = this
        $.ajax({
          type: 'post',
          url: test + '/api/campaigns/del_abexp',
          async: true,
          data: JSON.stringify({
            campaign_id: Number(this.$route.params.campaign_id),
            ab_id: Number(row.id),
            user_name: JSON.parse(sessionStorage.getItem('name')) }),
          headers: {
            'Content-Type': 'application/json',
            Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
          },
          timeout: 5000,
          success: function (data) {
            if (data.code === '999999') {
              self.$message({
                message: '删除成功',
                center: true,
                type: 'success'
              })
            } else {
              self.$message.error({
                message: data.msg,
                center: true
              })
            }
            self.getAbExpList()
          }
        })
      }).catch(() => {
      })
    },
    // 获取实验候选
    getExpAltList (expId) {
      let self = this
      $.ajax({
        type: 'get',
        url: test + '/api/campaigns/case_exp_altlist',
        async: true,
        data: { campaign_id: this.$route.params.campaign_id,
          ab_id: expId },
        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          if (data.code === '999999') {
            self.altListData = data.data.data
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },
    handleCurrentChange (val) {
      this.page = val
      this.getAbExpList()
    }
  },
  mounted () {
    this.getAbExpList()
    this.getCampaignMata()
    // this.project = this.$route.params.project_id
    // this.case = this.$route.params.case_id
    // if (this.$route.params.firstID) {
    //   this.firstID = this.$route.params.firstID
    // } else {
    //   this.firstID = 'all'
    //  }
  // this.firstID = this.$route.params.firstGroup
  // console.log(this.firstID, 'this.$route.params.project_id')
  }
}
</script>

<style lang="scss" scoped>
  p{
      margin: 0 0 10px;
  }
  .return-list {
      margin-top: 0px;
      margin-left: 10px;
      margin-right: 10px;
      margin-bottom: 10px;
      border-radius: 0px;
  }
  .HttpStatus {
      border-radius: 25px;
      padding: 10px;
      box-sizing: border-box;
      color: #fff;
      font-size: 15px;
      background-color: #409eff;
      text-align: center;
      margin-right: 10px;
  }
  .resultStyle{
      margin-left: 2%;
      margin-top: 10px;
  }
  .lin{
      left: 2%;
      border: 1px solid #e6e6e6;
      width: 90%;
      position: relative
  }
</style>
